			height: .4rem;
			line-height: .4rem;
			display: flex;
			justify-content: flex-end;
			box-sizing: border-box;
			padding-right: .1rem;
			border-bottom: 1px solid $alert_border;
		}
		.content {
			width: 100%;
			text-align: center;
			.shouji {
				color: #9097ef;
				margin: .1rem 0;
				.white {
					color: #fff;
					margin: 0 .05rem;
				}
			}
			.phone {
				font-size: .15rem;
				margin: .1rem 0;
			}
			.blue {
				color: #9097ef;
				margin: .15rem 0;
			}
		}
		.bottom {
			width: 100%;
			height: .4rem;
			display: flex;
			border-top: 1px solid $alert_border;
			p {
				width: 50%;
				line-height: .4rem;
				text-align: center;
				box-sizing: border-box;
				a {
					color: #fff;
					outline: none;
				}
			}
			p:nth-of-type(1) {
				border-right: 1px solid $alert_border;
			}
		}
	}
</style>



