<template>
	<div id="Aboutus">
		<my-header :text="headerText"></my-header>
		<div v-show="loadingIsShow">
			<my-loading></my-loading>
		</div>
		<!---->
		<div class="meng_ban" v-show="upgradeIsShow"></div>
		<div id="SetUp" v-show="upgradeIsShow">
			<div class="Setup_header">
				<span>{{$t("userCenter.Versionupdate")}}</span>
				<span>{{newVersion}}</span>
			</div>
			<div class="Setup_content">
				{{versionContent}}
			</div>
			<div class="bottom">
				<p v-show="'forceIsShow'" @click="upgradeIsShow=false">{{$t("capital.Cancel")}}</p>
				<p @click="topVersion">{{$t("userCenter.Update")}}</p>
			</div>
		</div>
		<!---->
		<div class="meng_ban" v-show="Newest"></div>
		<div id="SetUp" v-show="Newest">
			<div class="Setup_header">
				<span>{{$t("userCenter.Versionupdate")}}</span>
			</div>
			<div class="Setup_content">
				{{$t("userCenter.Thelatestversion")}}
			</div>
			<div class="bottom">
				<p @click="Newest=false">{{$t("userCenter.determine")}}</p>
			</div>
		</div>

		<!--     -->
		<div class="Aboutus_content">
			<ul>
				<router-link to="/ProductFeedback" tag="li">
					<span>{{$t("userCenter.Productsuggestion")}}</span>
					<i class="iconfont icon-iconfontjiantou5"></i>
				</router-link>
				<li @click="translate" v-show="islocation">
					<i class="icon_red" v-show="isUpdate"></i>
					<span>{{$t("userCenter.Versionupdating")}}</span>
					<span>{{$t("userCenter.Currentversion")}}: V.{{version}}</span>
				</li>
			</ul>
		</div>

	</div>
</template>

<script>
	import header from "../../public/backHeader"
	import { Toast, MessageBox } from 'mint-ui'
	import loading from "../../public/loading";
	import { mapGetters,mapActions,mapState,mapMutations } from 'vuex'
	export default {
		data() {
			return {
				headerText: this.$t("userCenter.Aboutus"),
				version: "",
				/**/
				versionAddress: "",
				/**/
				versionContent: "",
				/**/
				upgradeIsShow: false,
				/**/
				newVersion: "",
				/**/
				Newest: false,
				/**/
				islocation: true,
				/**/
				loadingIsShow: false,
				/*loading*/
				newVersionUrl: "", /**/
        isUpdate:false, /**/
			}
		},
		computed:{
			...mapState('setting',['version']),
		},
		mounted() {
			let _this = this;
			/**/
			var u = navigator.userAgent;
      if(window.plus) {
        plus.runtime.getProperty(plus.runtime.appid, function(wgtinfo) {
          _this.version = wgtinfo.version;
        });
        if(u.indexOf('iPhone') > -1) { //
          this.$ajax.get(this.httpUrl.getVserion, {
            params: {
              type: 0
            }
          }).then(function(res) {
            _this.newVersion = res.data.data.version_code; /**/
            _this.newVersionUrl = res.data.data.down_url; /**/
            _this.versionContent = res.data.data.content; /**/
            if(_this.version!=_this.newVersion){ /**/
              _this.isUpdate = true;
            }
          }, function(err) {
            console.log(err)
          })
        } else {
          this.$ajax.get(this.httpUrl.getVserion, {
            params: {
              type: 1
            }
          }).then(function(res) {
            _this.newVersion = res.data.data.version_code; /**/
            _this.newVersionUrl = res.data.data.down_url; /**/
            _this.versionContent = res.data.data.content; /**/
            if(_this.version!=_this.newVersion){
              _this.isUpdate = true;
            }
          }, function(err) {
            console.log(err)
          })
        }
      }


			if(window.location.host) {
				this.islocation = false;
			}
		},
		components: {
			"my-header": header,
			"my-loading": loading
		},
		methods: {
			topVersion() { /**/
				let _this = this;
				/**/
				var u = navigator.userAgent;
				if(u.indexOf('iPhone') > -1) {//
					window.open("itms-services://?action=download-manifest&url=https://www.imx.com/manifest.plist");
					return false;
				}
				_this.loadingIsShow = true;
				var dtask = plus.downloader.createDownload(_this.newVersionUrl, {
					method: "GET"
				}, function(d, status) {
					if(status == 200) { //
						_this.loadingIsShow = false;
						Toast(_this.$t("userCenter.Downloadsuccee"));
						plus.runtime.install(d.filename, {
							method: "GET"
						}, function() {
							Toast(_this.$t("userCenter.Updatesuccess"));
							setTimeout(function() {
								plus.runtime.restart();
								//plus.runtime.quit(); /**/
							}, 1000)
						}, function(err) {
							Toast(err.message);
						});
					} else {
						_this.loadingIsShow = false;
						//,,
						Toast(_this.$t("userCenter.Downloadfailed"));
						/**/
						//dtask.abort();//:,;(,delFile());
					}
				}); //
				dtask.start();
			},
			translate() { /**/
				if(this.newVersion != this.version) { /**/
					this.upgradeIsShow = true; /**/
				} else {
					this.Newest = true; /**/
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	#Aboutus {
		width: 100%;
		height: 100%;
		color: #fff;
	}
	/**/

	$border_bottom:0.01rem solid #1b1d40;
	/**/

	.Aboutus_content {
		width: 100%;
		ul {
			width: 100%;
			li {
				width: 100%;
				height: .4rem;
				display: flex;
				justify-content: space-between;
				align-items: center;
				background: #141738;
				box-sizing: border-box;
				padding: 0 .1rem;
				border-bottom: $border_bottom;
				position: relative;
				.icon_red {
					width: .05rem;
					height: .05rem;
					background: red;
					border-radius: 50%;
					position: absolute;
					left: 20%;
					top: 20%;
				}
			}
		}
	}
	/**/

	.meng_ban {
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		position: fixed;
		left: 0;
		top: 0;
		z-index: 999;
	}
	/**/

	#SetUp {
		width: 90%;
		background: #323560;
		color: #fff;
		position: fixed;
		left: 5%;
		top: 20%;
		z-index: 9999;
		/**/
		.Setup_header {
			width: 100%;
			height: .35rem;
			display: flex;
			justify-content: space-between;
			box-sizing: border-box;
			align-items: center;
			padding: 0 .1rem;
			border-bottom: 0.01rem solid #1F2353;
		}
		/**/
		.Setup_content {
			width: 100%;
			box-sizing: border-box;
			padding: .1rem .1rem;
		}
		/**/
		.bottom {
			width: 100%;
			height: .35rem;
			display: flex;
			line-height: .35rem;
			justify-content: center;
			border-top: 0.01rem solid #1f2353;
			p {
				flex: 1;
				text-align: center;
				box-sizing: border-box;
			}
			p:nth-of-type(1) {
				border-right: 1px solid #1F2351;
			}
		}
	}
</style>



