<template>
	<div id="app">
		<router-view/>
		<div class="isTui" v-show="isTui">
			<p>{{$t("userCenter.ClickingExit")}}</p>
		</div>
    <!---->
    <div class="BrokenNetwork" v-show="isNetwork">
      <img src="./assets/wifi@3x.png" alt="">
      <p class="noNetwork">{{$t("noNetWork.Network")}}</p>
      <p class="blue" @click="reaold">{{$t("noNetWork.Reload")}}</p>
    </div>
  </div>
</template>

<script>
	import loading from "@/components/public/loading"; /*loading*/
	import store from "@/store.js"; /*store*/
	// import VueI18n from 'vue-i18n';
	import { mapGetters,mapActions,mapState,mapMutations } from 'vuex'
	export default {
		name: 'App',
		data() {
			return { /**/
				isTui: false,
        isNetwork:false,  /**/
			}
		},
		methods: {
			...mapMutations('user',['setData','logout']),
			initData() {
				this.$store.commit('initbgColor');
			},
      reaold(){
        window.location.reload();
      }
		},
		computed: {
		...mapState('user', [
			'userID',
			'orderID'
	  ]),
			...mapGetters([
			"bgColor"
		])
		},
    watch: { /**/
      $route() {
        // if(this.$route.name=="forget" || this.$route.name=="register"){  //
        //   window.localStorage.setItem("isRegister",true);
        // }
        // if(this.$route.name!="forget" && this.$route.name!="register" && this.$route.name!="login"){ //
        //   window.localStorage.removeItem("isRegister");
        // }
        // if(window.localStorage.getItem("loginSuccess")){
        //   window.location.reload();
        //   window.localStorage.removeItem("loginSuccess");
        // }
      }
    },
		mounted() {
			let _this = this;
			/**/
      document.querySelector("#app").addEventListener("touchmove",function (event) {
        event.stopPropagation();
      })
      /* */
      /*  ---*/
      document.body.style.background = "#111627";
      /**/
			/**/
			_this.initData();
			/**/
			document.addEventListener('plusready', function() {
				// H5 plus
				// 
				plus.navigator.setStatusBarBackground('#121528');
				plus.navigator.setStatusBarStyle('light')
				/**/
				document.addEventListener("pause", function() {
					/*******************start************/
					_this.ws.onmessage = function(e) {
						var arr = JSON.parse(e.data);
						if(arr.type == "ping") { ////type
							_this.ws.send('{"pong":' + Date.now() + '}');
						} else if(arr.type == "otc") {
							if(arr.content != null || arr.content != undefined) {
								// window.localStorage.setItem("OrderId", arr.content.orderId);
								_this.setData({'orderID':arr.content.orderId});
								let otext;
								if(arr.content.type != 1) { /**/
									otext = arr.content.content;
								} else {
									otext = ""
								}
								plus.push.createMessage(otext, '', {
									title: "otc "
								});
								plus.push.addEventListener("click", function(msg) {
									// msg.payload
									if(!_this.userID) {
										_this.$router.push({
											name: "fbdd"
										})
										return false;
									}
									/**/
									_this.$ajax.get(_this.httpUrl.getddxqUrl, {
										headers: {
											'Authorization': 'Bearer ' + _this.token
										},
										params: {
											"blaceId": _this.orderID//window.localStorage.getItem("OrderId")
										}
									}).then(function(res) {
										if(res.data.data.pur_user == _this.userID) { /*idid*/
											/**/
											if(res.data.data.type == 1) { //
												_this.$router.push({
													name: "sellddxq"
												})
											} else {
												_this.$router.push({
													name: "buyddxq"
												})
											}
										} else {
											/**/
											if(res.data.data.type == 1) { //
												_this.$router.push({
													name: "buyddxq"
												})
											} else {
												_this.$router.push({
													name: "sellddxq"
												})
											}
										}
									}, function(err) {
										console.log(err)
									})

								}, false);

							}
						}
					}
					/*******************end************/

				}, false);
				/**/

				/**/
				var webview = plus.webview.currentWebview();
				plus.key.addEventListener('backbutton', function() {
					webview.canBack(function(e) {
						if(e.canBack && _this.$route.name != "home" && _this.$route.name != "bbjy" && _this.$route.name != "otc" && _this.$route.name != "userCenter") {
							webview.back();
						} else {
							//
							//1
							var first = null;
							plus.key.addEventListener('backbutton', function() {
								//
								if(!first) {
									first = new Date().getTime(); /**/
									_this.isTui = true; /**/
									setTimeout(function() {
										_this.isTui = false;
										first = null;
									}, 1000);
								} else {
									/**/
									if(new Date().getTime() - first < 1500) {
										var u = navigator.userAgent,
											app = navigator.appVersion;
										var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
										var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
										/**/
										if(isIOS) {
											plus.runtime.quit();
											window.localStorage.removeItem("token");
											window.localStorage.removeItem("refresh_token");
											window.localStorage.removeItem("userID");
											window.localStorage.removeItem("username");
											window.localStorage.removeItem("OrderId");
											_this.$router.push('/'); ///
											_this.logout();
											// window.location.reload();
										}
										if(isAndroid) {
											plus.runtime.quit();
											plus.android.runtimeMainActivity().moveTaskToBack(false);
											window.localStorage.removeItem("token");
											window.localStorage.removeItem("refresh_token");
											window.localStorage.removeItem("userID");
											window.localStorage.removeItem("username");
											window.localStorage.removeItem("OrderId");
											_this.$router.push('/'); ///
											_this.logout();
											// window.location.reload();
										}
									}

								}
							}, false);
						}
					})
				});
			});
			/**/
			// 
			this.$ajax.interceptors.response.use((response) => {
				if(response.data.status != undefined) {
					if(Number(response.data.status.code) == -200) {
						_this.$message.error({
							message: response.data.status.message,
							type: "error"
						});

					}
					if(Number(response.data.status.code) != 200) {
						_this.$message.error({
							message: response.data.status.message,
							type: "error"
						});
					}
				}
				if(response.status == 200) {
					_this.loadingIsShow = false;
				}
				return response;
			}, (error) => {
        if (!error.response || error.response.status === 408 || error.code === 'ECONNABORTED') {
          _this.isNetwork = true;
        }else{
          this.isNetwork = false;
        }
				if(error.response) {
					_this.loadingIsShow = false;
					switch(error.response.status) {
						case 401:
							// // token
							window.localStorage.removeItem("token");
							window.localStorage.removeItem("refresh_token");
							window.localStorage.removeItem("curTime");
              window.localStorage.removeItem("vuex");
							_this.$router.push({ /**/
								name: "bbjy",
								params: {
									id: _this.matchcoin_id
								}
							});
							_this.logout();
							// window.location.reload();
							break;
						case 504:
							_this.loadingIsShow = false;
							_this.$message.error({
								message: '',
								type: "error"
							});
							break;
						case 500:
							_this.loadingIsShow = false;
							_this.$message.error({
								message: '',
								type: "error"
							});
							break;
					}
				}
				return Promise.reject(error)
			})
		}
	}
</script>

<style lang="scss" scoped>
	#app {
		width: 100%;
		height: 100%;
    background: #111627;
    padding-bottom: env(safe-area-inset-bottom);
    overflow-y: auto;
  }
	/**/
	.isTui {
		width: 60%;
		height: .5rem;
		background: #202350;
		color: #fff;
		position: fixed;
		left: 25%;
		top: 35%;
		z-index: 8889;
		text-align: center;
		line-height: .5rem;
	}
	/**/

	.Preservation_success {
		width: 60%;
		text-align: center;
		height: .4rem;
		line-height: .4rem;
		background: #4b5090;
		color: #ffff;
		position: fixed;
		bottom: 20%;
		left: 20%;
		z-index: 9999;
		font-size: .15rem;
	}
  /**/
  .BrokenNetwork{
    width: 100%;
    height: 100%;
    background: #181a31;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 999;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    img{
      width: .4rem;
      height: .35rem;
    }
    p{
      font-size: .14rem;
      margin-top: .2rem;
    }
    .noNetwork{
      color: #686b94;
    }
    .blue{
      color: #65c3c2;
    }
  }
</style>



