<template>
	<div id="chat">
		<!---->
		<my-header :text="header"></my-header>
		<!--loading-->
		<div v-show="loadingIsShow">
			<my-loading></my-loading>
		</div>
		<!---->
		<div class="oImg_men" v-show="imgIsShow"></div>
		<div class="oImg_content" v-show="imgIsShow">
			<div class="img">
				<p class="delete">
					<i class="iconfont icon-cuowu" @click="imgIsShow=false"></i>
				</p>
				<img :src="imgSrc" />
				<p class="download" @click="updown">
					<i class="iconfont icon-xiazai"></i>
				</p>
			</div>
		</div>

		<!---->
		<div class="chat_header">
			<div class="oleft">
				<span class="currency">{{$t("list.buy")}}{{pageIdentifier}}</span>
			</div>
			<div class="content">
				<p class="money">{{$t("bbjy.Amount")}}</p>
				<p class="Num">
					<span class="text">{{OrderList.price}}</span>
					<span>{{legal.code}}</span> 
				</p>
			</div>
			<div class="oright">
				<span class="orange" v-show="OrderList.status==0">{{$t("fbjy.WaitingPayment")}}</span>
				<span class="orange" v-show="OrderList.status==-1">{{$t("fbjy.Canceled")}}</span>
				<span class="orange" v-show="OrderList.status==1">{{$t("fbjy.HavePaid")}}</span>
				<span class="success" v-show="OrderList.status==3">{{$t("fbjy.Completed")}}</span>
			</div>
		</div>
		<!---->
		<div class="chat_content" id="scrollBox" ref="scrollBox">
			<div class="loading" v-if="loadingData">
				<img src="../../../assets/loading.gif" />
			</div>
			<ul id="move" @touchstart="start($event)" @touchmove="move($event)" @touchend="end($event)">
				<dl v-for="item in ChatRecord" class="BoxContent" :class="{'messageOleft':item.type!=2&&item.sendUid!=userId,'messageOright':item.type!=2&&item.sendUid==userId}">
					<!---->
					<div class="system" v-if="item.type==2&&item.toUid==userId">
						<dl class="text">
							<dd>{{$t("fbjy.SystemRemindsYou")}}:{{item.content}}</dd>
							<dt class="time">{{formatTime(item.created_at).split(" ")[1]}}</dt>
						</dl>
					</div>
					<!---->
					<div class="time" v-show="item.type!=2&&item.sendUid!=userId || item.type!=2&&item.sendUid==userId">
						<span>{{formatTime(item.created_at).split(" ")[1]}}</span>
					</div>
					<!---->
					<dd class="touxiang" v-show="item.type!=2&&item.sendUid!=userId">
						<i class="iconfont icon-icontouxiang"></i>
					</dd>
					<li class="oleft" v-show="item.type!=2&&item.sendUid!=userId">
						<img :src="item.content" v-show="item.content.indexOf('http')!=-1" @click="imgShow(item.content)" />
						<p class="text" v-if="item.content.indexOf('http')==-1">
							{{item.content}}
						</p>
					</li>
					<!---->
					<li class="oright" v-show="item.type!=2&&item.sendUid==userId">
						<img :src="item.content" v-show="item.content.indexOf('http')!=-1" @click="imgShow(item.content)" />
						<p class="text" v-if="item.content.indexOf('http')==-1">
							{{item.content}}
						</p>
					</li>
					<dd class="touxiang" v-show="item.type!=2&&item.sendUid==userId">
						<i class="iconfont icon-icontouxiang"></i>
					</dd>
				</dl>
			</ul>
		</div>

		<!---->
		<div class="chat_bottom">
			<input type="text" v-model="content" class="oText" />
			<div class="oImg1" v-show="locdingIsShow">
				<img src="../../../assets/loading2.gif" />
			</div>
			<div class="oImg" v-show="!locdingIsShow">
				<i class="iconfont icon-tupian"></i>
				<input type="file" accept="image/png,image/gif,image/jpg,image/jpeg" @change="sendImg($event)" />
			</div>
			<div class="SendOut">
				<img src="../../../assets/loading3.gif" v-show="locdingText" />
				<button v-show="!locdingText" @click="sendText">{{$t("fbjy.Send")}}</button>
			</div>
		</div>
	</div>
</template>

<script>
	import header from "../../public/backHeader"
	import loading from "../../public/loading"
	import { Toast, MessageBox } from 'mint-ui';
	import { mapGetters,mapActions,mapState,mapMutations } from 'vuex'
	import $ from "jquery";
	export default {
		data() {
			return {
				header: "",
				/*id*/
				OrderId: this.$route.params.id,
				/*id*/
				toUserIds: "",
				/*id*/
				OrderList: {},
				/**/
				pageIdentifier: "",
				/**/
				content: "",
				/**/
				ChatRecord: [],
				/**/
				ChatTotal: 0,
				/**/
				CountPage: 1,
				/**/
				imgSrc: "",
				/**/
				imgIsShow: false,
				/**/
				Disable: false,
				/**/
				startY: 0,
				/*Y*/
				endY: 0,
				/*Y*/
				loadingData: false,
				/**/
				locdingIsShow: false,
				/*loading*/
				locdingText: false,
				/*locding*/
				loadingIsShow: false, /*loading*/
				legal:"",//
			}
		},
		computed:{
			...mapState('user',{'userId':state=>state.userID}),
			...mapState('user', [
				'orderID'
			]),
		},
		components: {
			"my-header": header,
			"my-loading": loading
		},
		methods: {
			...mapMutations('user',['setData']),
			imgIsShowClick(imgSrc) {
				let _this = this;
				//
				var dtask = plus.downloader.createDownload(imgSrc, {
					method: "GET"
				}, function(d, status) {
					if(status == 200) { //
						_this.loadingIsShow = false;
						plus.gallery.save(d.filename, function() { //
							Toast(_this.$t("fbjy.DownloadSuccessful"));
						}, function() {
							Toast(_this.$t("userCenter.Downloadfailed"));
						});
					} else {
						_this.loadingIsShow = false;
						//,,
						Toast(_this.$t("userCenter.Downloadfailed"));
						/**/
						//dtask.abort();//:,;(,delFile());
					}
				}); //
				dtask.start();
			},
			updown() { /**/
				let _this = this;
				if(window.location.host) { /**/
					window.open(_this.imgSrc)
					return false;
				} else {
					//
					_this.loadingIsShow = true;
					_this.imgIsShowClick(_this.code_img);
				}
			},
			/**/
			start(e) {
				var touch = event.targetTouches[0];
				this.startY = touch.pageY; /*----*/
			},
			/**/
			move(e) {
				let _this = this;
				let touch = e.targetTouches[0];
				this.endY = touch.pageY; /*Y  ----*/
			},
			/**/
			/**/
			end(e) {
				let _this = this;
				let distanceY = this.endY - this.startY;
				//
				var clientHeight = document.documentElement.clientHeight;
				//
				if(this.startY != Math.abs(distanceY)) {
					//20%
					if(Math.abs(distanceY) > clientHeight * 0.2 && _this.scroll.scrollTop == 0) {
						if(this.Disable) {
							_this.$message({
								showClose: true,
								message: _this.$t("fbjy.NoMoreRecords"),
								type: 'success',
								duration: 2000
							});
							return false;
						}
						this.loadingData = true;
						/**/
						this.CountPage++;
						this.$ajax.get(this.httpUrl.getltjlUrl, {
							headers: {
								'Authorization': 'Bearer ' + _this.token
							},
							params: {
								"blaceId": _this.OrderId,
								"page": _this.CountPage
							}
						}).then(function(res) {
							_this.loadingData = false;
							let arr = res.data.data.data; /**/
							for(let i = 0; i < arr.length; i++) { /**/
								_this.ChatRecord.unshift(arr[i])
							}
							if(_this.CountPage >= res.data.data.last_page) {
								_this.Disable = true;
							}
						}, function(err) {
							console.log(err)
						})
					}
				}
			},
			/**/
			imgShow(src) {
				this.imgSrc = src;
				this.imgIsShow = true;
			},
			/**/
			sendText() {
				let _this = this;
				if(this.content == "") {
					_this.$message({
						message: _this.$t("fbjy.ContentNotEmp"),
						type: 'warning',
						duration: 2000
					});
					return false;
				}
				this.locdingText = true; /*loading*/
				setTimeout(function() {
					_this.locdingText = false;
				}, 1500)
				/**/
				this.$ajax.post(this.httpUrl.postltxxUrl, {
					toUserIds: _this.toUserIds,
					/*ID*/
					orderId: _this.OrderId,
					/*ID*/
					msgType: 0,
					content: _this.content
				}, {
					headers: {
						'Authorization': 'Bearer ' + _this.token
					}
				}).then(function(res) {
					/**/
					_this.ChatRecord.push(res.data.data);
					_this.$nextTick(() => {
						_this.scroll.scrollTop = _this.scroll.scrollHeight;
						_this.content = "";
					})
					_this.locdingText = false;
				}).catch(function(err) {
					console.log(" err", err)
				})
			},
			/* */
			sendImg(e) {
				let _this = this;
				var file = e.target.files[0];
				/**/
				if((!/image\/\w+/.test(file.type))) {
					this.ltInfo = _this.$t("userCenter.UploadedIsNotPhoto");
					this.isltInfo = true;
					setTimeout(() => {
						this.isltInfo = false;
					}, 1000)
					return false;
				}
				//
				if(file.size > 2 * 1024 * 1024) {
					_this.$message({
						title: _this.$t("fbjy.Warning"),
						message: _this.$t("fbjy.UploadedPhoLargeselect"),
						type: 'warning',
						duration: 2000
					});
					return false;
				}
				this.locdingIsShow = true;
				setTimeout(function() {
					_this.locdingIsShow = false;
				}, 2000)
				var param = new FormData(); //form
				param.append('picture', file); //appendform
				param.append('toUserIds', this.toUserIds);
				param.append('orderId', this.OrderId);
				param.append('msgType', 1);
				this.$ajax.post(this.httpUrl.postltxxUrl, param, {
					headers: {
						'Authorization': 'Bearer ' + _this.token
					}
				}).then(function(res) {
					_this.locdingIsShow = false;
					/**/
					_this.ChatRecord.push(res.data.data);
					/**/
					_this.$nextTick(() => {
						_this.scroll.scrollTop = _this.scroll.scrollHeight;
					})

					/**/
				}).catch(function(err) {
					console.log(" err", err)
				})
			}
		},
		mounted() {
			let _this = this;
			/*box*/
			this.scroll = document.querySelector("#scrollBox");
			/**/
			this.$ajax.get(this.httpUrl.getddxqUrl, {
				headers: {
					'Authorization': 'Bearer ' + _this.token
				},
				params: {
					"blaceId": _this.OrderId
				}
			}).then(function(res) {
				_this.legal = res.data.data.legal;
				_this.OrderList = res.data.data;
				_this.pageIdentifier = res.data.data.currency.pageIdentifier; /**/
				// id
				if(_this.userId == res.data.data.user_id) {
					_this.toUserIds = res.data.data.pur_user;
					_this.header = res.data.data.merchant.name;
				} else {
					_this.toUserIds = res.data.data.user_id;
					_this.header = res.data.data.user.name;
				}
			}, function(err) {
				console.log(err)
			})
			/**/
			this.$ajax.get(this.httpUrl.getltjlUrl, {
				headers: {
					'Authorization': 'Bearer ' + _this.token
				},
				params: {
					"blaceId": _this.OrderId,
					"page": 1
				}
			}).then(function(res) {
				if(res.data.status.code == 200) { //ChatRecord
					var arr = res.data.data.data;
					/**/
					for(let i = 0; i < arr.length; i++) {
						_this.ChatRecord.unshift(arr[i])
					}
				}
				/**/
				_this.$nextTick(() => {
					_this.scroll.scrollTop = _this.scroll.scrollHeight;
					_this.content = "";
				})
				/**/
			}).catch(function(err) {
				console.log(" err", err)
			})
			/**********************************************************start*************************************************/

			_this.ws.onmessage = function(e) {
				var arr = JSON.parse(e.data);
				if(arr.type == "ping") { ////type
					_this.ws.send('{"pong":' + Date.now() + '}');
				} else if(arr.type == "otc") {
					// msgTypemsgTypemsgType"reqUnReadMsgCount""testMsg""tokenExpired"apushmsgType
					if(window.plus) {
						if(_this.OrderId != arr.content.orderId) {
							// window.localStorage.setItem("OrderId", arr.content.orderId);
							_this.setData({'orderID':arr.content.orderId});
							let otext;
							if(arr.content.type != 1) { /**/
								otext = arr.content.content;
							} else {
								otext = _this.$t("bbjy.Systemmessage")
							}
							plus.push.createMessage(otext, '', {
								title: "otc" + _this.$t("bbjy.Haveneworder")
							});
							plus.push.addEventListener("click", function(msg) {
								// msg.payload
								/**/
								_this.$ajax.get(_this.httpUrl.getddxqUrl, {
									headers: {
										'Authorization': 'Bearer ' + _this.token
									},
									params: {
										"blaceId": _this.orderID//window.localStorage.getItem("OrderId")
									}
								}).then(function(res) {
									if(res.data.data.pur_user == _this.userId) { /*idid*/
										/**/
										if(res.data.data.type == 1) { //
											_this.$router.push({
												name: "sellddxq"
											})
										} else {
											_this.$router.push({
												name: "buyddxq"
											})
										}
									} else {
										/**/
										if(res.data.data.type == 1) { //
											_this.$router.push({
												name: "buyddxq"
											})
										} else {
											_this.$router.push({
												name: "sellddxq"
											})
										}
									}
								}, function(err) {
									console.log(err)
								})
							}, false);
						}
					}
					if(_this.OrderId != arr.content.orderId) {
						return false;
					} else {
						/**/
						_this.ChatRecord.push(arr.content);
						/**/
						_this.$nextTick(() => {
							_this.scroll.scrollTop = _this.scroll.scrollHeight;
						})
					}
				}
			}
			/**********************************************************end***************************************************/

		}
	}
</script>

<style lang="scss" scoped>
	$orange:#ff4800;
	/**/

	$success:#02c27c;
	/**/
	/**/

	.orange {
		color: $orange;
	}
	/**/

	.success {
		color: $success;
	}

	#chat {
		width: 100%;
		height: 100%;
		color: #fff;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	/**/

	.chat_header {
		width: 100%;
		height: .5rem;
		display: flex;
		line-height: .5rem;
		background: #141738;
		justify-content: space-between;
		padding-bottom: .05rem;
		div {
			text-align: center;
		}
		.content {
			text-align: left;
			.money,
			.Num {
				line-height: .25rem;
				.text {
					font-size: .16rem;
				}
			}
			.money {
				color: #6178b1;
			}
		}
		.oleft {
			box-sizing: border-box;
			padding-left: .1rem;
		}
		.oright {
			text-align: right;
			box-sizing: border-box;
			padding-right: .1rem;
		}
	}
	/**/

	.chat_content {
		flex: 1;
		background: #171933;
		/*overflow-x: hidden;*/
		overflow: auto;
		box-sizing: border-box;
		padding: 0 .1rem;
		.loading {
			text-align: center;
		}
		ul {
			width: 100%;
			position: relative;
			.system {
				width: 100%;
				display: flex;
				justify-content: center;
				padding: .1rem 0;
				.text {
					background: #464593;
					color: #fff;
					box-sizing: border-box;
					padding: 6px 10px;
					word-break: break-all;
					border-radius: 4px;
					line-height: 1.8;
					.time {
						text-align: right;
					}
				}
			}
			/**/
			.messageOleft {
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				padding: .1rem 0;
				.time {
					width: 100%;
					text-align: center;
					margin: 0 0 .05rem 0;
				}
				.touxiang {
					width: .45rem;
					height: .45rem;
					text-align: center;
					line-height: .45rem;
					background: #fff;
					.iconfont {
						font-size: .25rem;
						color: #2f2e67;
					}
					margin-right:.1rem;
				}
			}
			/**/
			.messageOright {
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				justify-content: flex-end;
				padding: .1rem 0;
				.time {
					width: 100%;
					text-align: center;
					margin: 0 0 .05rem 0;
				}
				.touxiang {
					width: .45rem;
					height: .45rem;
					text-align: center;
					line-height: .45rem;
					background: #fff;
					.iconfont {
						font-size: .25rem;
						color: #2f2e67;
					}
					margin-left:.1rem;
				}
			}
			.oleft,
			.oright {
				display: inline-block;
				position: relative;
				padding: 0 10px;
				max-width: calc(100% - .85rem);
				min-height: .2rem;
				line-height: 1.8;
				font-size: .12rem;
				padding: 6px 10px;
				text-align: left;
				word-break: break-all;
				background-color: #fff;
				color: #000;
				display: flex;
				border-radius: 4px;
				box-shadow: 0px 1px 7px -5px #000;
				align-items: center;
				.text {
					word-wrap: break-word;
					word-break: break-all;
				}
				img {
					width: 90%;
					display: block;
					margin: 0 auto;
				}
			}
			.oleft:before {
				content: " ";
				position: absolute;
				top: 9px;
				right: 100%;
				border: 6px solid transparent;
				border-right-color: #fff;
			}
			.oright:before {
				content: " ";
				position: absolute;
				top: 9px;
				left: 100%;
				border: 6px solid transparent;
				border-left-color: #fff;
			}
		}
	}
	/**/

	.chat_bottom {
		width: 100%;
		height: .45rem;
		background: #fff;
		display: flex;
		box-sizing: border-box;
		align-items: center;
		padding: 0 .05rem;
		.oText {
			width: 65%;
			height: 60%;
			border: none;
			border-bottom: .01rem solid #adacc3;
			color: #000;
		}
		.oImg1 {
			width: 18%;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		.oImg {
			width: 18%;
			position: relative;
			input {
				width: 95%;
				height: .45rem;
				opacity: 0;
			}
			.iconfont {
				color: #000;
				position: absolute;
				left: 20%;
				top: 20%;
				width: .25rem;
				height: .25rem;
				font-size: .25rem;
				color: #2f2e67;
			}
		}
		.SendOut {
			width: 18%;
			height: 100%;
			line-height: .45rem;
			display: flex;
			align-items: center;
			justify-content: center;
			button {
				width: .48rem;
				height: .27rem;
				border: none;
				background: #2f2e67;
				text-align: center;
				line-height: .2rem;
			}
		}
	}
	/**/

	.oImg_men {
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.8);
		position: fixed;
		left: 0;
		top: 0;
		z-index: 888;
	}

	.oImg_content {
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 889;
		.img {
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			img {
				max-width: 98%;
				max-height: 98%;
			}
			.download {
				position: absolute;
				right: .1rem;
				bottom: .1rem;
				width: .4rem;
				height: .4rem;
				background: rgba(255, 255, 255, 0.5);
				color: #000;
				border-radius: 50%;
				text-align: center;
				line-height: .4rem;
				.iconfont {
					font-size: .23rem;
				}
			}
			.delete {
				position: absolute;
				right: .1rem;
				top: .1rem;
				width: .4rem;
				height: .4rem;
				background: #000;
				color: #fff;
				border-radius: 50%;
				text-align: center;
				line-height: .4rem;
				.iconfont {
					font-size: .2rem;
				}
			}
		}
	}
</style>



