<template>
  <div
    class="scroller"
    ref="scroller"
    @scroll="outterscroll"
    @touchstart="tchstart"
    @touchmove="tchmove"
    @touchend="tchend"
  >
    <div class="top_title">
      <slot name="top">
        <span class="spinner">
        	{{$t("login.Refreshing")}}...
        </span>
      </slot>
    </div>
    <div class="content_box"
      :style="{transform: `translateY(${pulldownlen || pulluplen}px)`, 'background-color': contentBackgroundColor}"
    >
      <slot class="inner"></slot>
    </div>
    <div class="bottom_box"  v-show="pullState===2">
      {{$t("login.Hardloading")}}...
    </div>
  </div>
</template>

<script>
export default {
  name: "scroller",
  data() {
    return {
      topPercent: 0, // 
      startPos: 0, // 
      limitPulldown: 50, // 
      limitPullup: 50, // 
      pulldownlen: 0, // 
      pulluplen: 0, // 
      pullState: 0, // , 0:  , 1:, 2:
      hasMore: true, // 
    };
  },
  props: {
    onInfinite: {
      type: Function,
      required: false,
      default: function(done){
        setTimeout(() => {
          done();
        }, 500);
      },
    }, // 
    onRefresh: {
      type: Function,
      required: false,
      default: function(done){
        this.onInfinite(done);
      },
    }, // 
    isFirstLoad: {
      // 
      type: Boolean,
      default: false,
    },
    bottomPosition: {
      // 
      type: Number,
      default: 0,
    },
    contentBackgroundColor: {
      // 
      type: String,
      default: '', // , css
    }
  },
  computed: {
    reachBottom() {
      // 
      return this.topPercent === 1;
    },
    reachTop() {
      // 
      return this.topPercent === 0;
    },
    loading() {
      return this.pullState ? true : false; // 0, 0
    }
  },
  methods: {
    // 
    outterscroll() {
      const scroller = this.$refs.scroller; // 
      let sTop = scroller.scrollTop; // 
      let sHeight = scroller.scrollHeight; // 
      let offsetHeight = scroller.offsetHeight; // 
      /* console.log({
        sTop,
        sHeight,
        offsetHeight
      }); */
      this.topPercent = sTop / (sHeight - offsetHeight); // ;
    },
    tchstart(e) {
      //  loading , 
      if (!this.loading) {
        this.pulldownlen = 0;
        this.pulluplen = 0;
        this.startPos = e.touches[0].clientY;
      } else {
        null;
      }
    },
    tchmove(e) {
      let len = e.touches[0].clientY - this.startPos;
      if (this.reachTop && len > 0) {
        // 
        this.pulluplen = 0;
        this.pulldownlen = len / 7;
      } else if (this.reachBottom && len < 0) {
        // 
        this.pulldownlen = 0;
        this.pulluplen = len / 7;
      }
    },
    tchend() {
      // ,  
      if (this.reachTop && this.pulldownlen > this.limitPulldown) {
        this.pulldownlen = this.limitPulldown;
        this.pullState = 1;
      } else {
        this.pulldownlen = 0;
      }
      // ,   , 
      if (this.reachBottom && this.pulluplen < -this.limitPullup && this.hasMore) {
        this.pulluplen = -this.limitPullup;
        this.pullState = 2;
      } else {
        this.pulluplen = 0;
      }
    },

    done(hasMore) {
      if(hasMore === false){
        this.hasMore = false;
      }else{
        this.hasMore = true;
      }
      //, 
      this.pulldownlen = 0;
      this.pulluplen = 0;
      this.pullState = 0;
    },
    //  
    triggerInfinite() {
      this.onInfinite && this.onInfinite(this.done);
    }
  },
  watch: {
    loading() {
      switch (this.pullState) {
        case 0: // 
          break;
        case 1: // 
          this.onRefresh && this.onRefresh(this.done);
          break;
        case 2: // 
          this.onInfinite && this.onInfinite(this.done);
          break;
        default:
          break;
      }
    }
  },
  created() {
    this.isFirstLoad && this.onInfinite(this.done); // 
  }
};
</script>

<style lang="less" scoped>
.scroller {
  width: 100%;
  height: 100%;
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;
  overflow-y: scroll;
  position: relative;
  .content_box {
    transition: all 0.2s ease-out;
    background-color: transparent;
    padding-bottom: 20px; /* , ,  */
  }
  .top_title {
    position: absolute;
    width: 100%;
    top: 0;
  }
  .bottom_box {
    position: fixed;
    bottom: .7rem;
    left: 0;
    width: 100vw;
    z-index: 6666;
    color: #fff;
    text-align: center;
  }
  span.spinner {
    display: block;
    width: 100%;
    height: .28rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    img{
    	width: .2rem;
    	height: .2rem;
    	margin-right: .1rem;
    }
  }
}

// animation
@keyframes round {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>




