Vue.prototype.formatTime = function(t) {
	//t,utc
	if(!t) return;
	//
	var d = new Date();
	// ()
	var timezone = d.getTimezoneOffset() / 60;
	t = t.replace(/-/g, "/"); //
	//
	var time = new Date(t);

	//                ----- (  -   )
	time.setHours(time.getHours() - (timezone));

	//
	var yy = time.getFullYear(); ///
	var MM = time.getMonth() + 1; ///
	MM = MM < 10 ? '0' + MM : MM; ///0
	var dd = time.getDate(); ///
	dd = dd < 10 ? '0' + dd : dd; ///0
	var hh = time.getHours(); ///
	hh = hh < 10 ? '0' + hh : hh; ///0
	var mm = time.getMinutes(); ///
	mm = mm < 10 ? '0' + mm : mm; ///0
	var ss = time.getSeconds(); ///
	ss = ss < 10 ? '0' + ss : ss; ///0
	var date = yy + '-' + MM + '-' + dd + ' ' + hh + ':' + mm + ':' + ss;
	return date;
}
////
Vue.prototype.formAdd = function(t) {
	//t,utc
	if(!t) return;
	//
	var d = new Date();
	//
	var timezone = d.getTimezoneOffset() / 60;
	t = t.replace(/-/g, "/"); //

	//
	var time = new Date(t);

	//                ----- (  +   )
	time.setHours(time.getHours() + (timezone));

	//
	var yy = time.getFullYear(); ///
	var MM = time.getMonth() + 1; ///
	MM = MM < 10 ? '0' + MM : MM; ///0
	var dd = time.getDate(); ///
	dd = dd < 10 ? '0' + dd : dd; ///0
	var hh = time.getHours(); ///
	hh = hh < 10 ? '0' + hh : hh; ///0
	var mm = time.getMinutes(); ///
	mm = mm < 10 ? '0' + mm : mm; ///0
	var ss = time.getSeconds(); ///
	ss = ss < 10 ? '0' + ss : ss; ///0
	var date = yy + '-' + MM + '-' + dd + ' ' + hh + ":00:00";
	return date;
}
